import _ from 'lodash';
import React, { Component } from 'react';
import ReactSlickCarousel from 'components/Templates/ReactSlickCarousel/ReactSlickCarousel';
// import { LazyLoadImage } from 'react-lazy-load-image-component';

import Logos from 'assets/images/InsurerLogos';

// import 'react-lazy-load-image-component/src/effects/opacity.css';
import './LandingInsurerCarousel.scss';

class LandingInsurerCarousel extends Component {
  render() {
    return (
      <div className="landing-logo-container">
        <ReactSlickCarousel
          lazyLoad
          autoplay
          arrows
          slidesToShow={8}
          className="white-sliders-padded"
          responsive={[{
            breakpoint: 576,
            settings: {
              slidesToShow: 3,
            },
          }, {
            breakpoint: 992,
            settings: {
              slidesToShow: 5,
            },
          }]}
        >
          {_.map(Logos.small, (data, key) => (
            <img
              key={`logo-carousel-${key}`}
              src={data}
              alt={`logo-carousel-${key}`}
              // effect="opacity"
            />
          ))}
        </ReactSlickCarousel>
      </div>
    );
  }
}

export default LandingInsurerCarousel;
